import React, {useRef, useEffect} from "react";

function Menu(props){

  let menuRef = useRef(null);
  let menuOpen = false;
  let navbarToggle = false;
  let navbar;

  useEffect(()=>{
    console.log(menuRef.current);
    addListiners(menuRef);
  }, []);

  function handleClick(){
  
    console.log("funciona");

    if(!navbarToggle){

      navbarToggle=true;
      navbar.classList.add("show");
    
    }else{

      navbarToggle=false;
      navbar.classList.remove("show");
    }

    console.log(menuRef.current.childNodes[0]);
    console.log(menuRef.current.children[0]);

    if(!menuOpen){
      menuOpen=true;
      menuRef.current.classList.add("active");
      menuRef.current.classList.remove("no-active");
    }else{
      menuOpen=false;
      menuRef.current.classList.add("no-active");
      menuRef.current.classList.remove("active");
    }

    /*
    menuRef.current.childNodes[0].children[1].style.background="white";
    */
  
  }
  function hoverIn(){
    menuRef.current.classList.add("hovered");
  }
  function hoverOut(){
    menuRef.current.classList.remove("hovered");
  }
  function addListiners(component){
  
    /*Initialize navbar otherwise it gets null*/
    navbar = document.getElementById("navbar");

    console.log("addListeners");
    console.log(navbar);


    component.current.classList.add("no-active");
    component.current.addEventListener("click", handleClick);
    component.current.addEventListener("mouseenter", hoverIn);
    component.current.addEventListener("mouseleave", hoverOut);
  
  }

  /*
  useEffect(addListiners(menuRef),[]);
  */

  return(
  
    <div id={props.id} className="menu-btn" ref={menuRef}>

      <div className="menu-lines">
        <div className="line line1"></div>
        <div className="line line2"></div>
        <div className="line line3"></div>
      </div>
    
    </div>
  
  );

}

export default Menu;



